<template>
  <div>

    <page-loading v-if="loading"></page-loading>

    <no-access v-if="!loading && !access" message="This service is not available for you"></no-access>

    <div v-if="!loading && access" class="row" style="z-index: 1000; padding: 0 12px">
      <div class="vx-card p-3">
        <div class="flex justify-content-between flex-wrap">
            <v-select @input="selectedFilter" class="sm-horizontal-center mr-2 ml-2" style="width: fit-content; min-width: 250px"
                      v-model="selectedSubject" :options="subjects" dir="ltr"/>

        </div>
      </div>
    </div>

    <no-data v-if="!loading && podcasts.length === 0" message="no podcast added to this subject"></no-data>

    <div v-if="!loading && access" class="row">

      <div class="col col-md-12 col-sm-12 col-12">
        <div class="row">
          <div :key="podcast.id" v-for="podcast in podcasts" class="col col-md-3 col-sm-12 col-12 mt-6">
            <vx-card>
              <img
                   @click="goToPodcastItem(podcast.fid)"
                   src="@/assets/images/custom/podcast-thumnail.png"
                   alt="content-img"
                   class="responsive rounded-lg cursor-pointer">


              <div class="text-center" style="height: 50px">
                <h5 @click="goToPodcastItem(podcast.fid)" class="cursor-pointer" style="direction: ltr">{{ podcast.item.title }}</h5>
              </div>
              <vs-divider></vs-divider>
              <div class="flex justify-between flex-wrap">
                <span>
                  <p class="text-grey">subject</p>
                  <p class="text-xl mt-2">{{ podcast.item.subject.title }}</p>
                </span>

                <div class="flex">

                  <vs-button class="mt-4"
                             color="primary"
                             type="border"
                             icon-pack="feather"
                             icon="icon-bookmark"
                             :id="'add-bookmark-btn-' + podcast.item.id"
                             v-if="podcast.bookmark === null"
                             @click="addToBookmark(podcast, `add-bookmark-btn-` + podcast.item.id)"
                  >
                  </vs-button>

                  <vs-button class="mt-4"
                             color="primary"
                             icon-pack="feather"
                             icon="icon-bookmark"
                             :id="`remove-bookmark-btn-` + podcast.item.id"
                             v-if="podcast.bookmark !== null"
                             @click="removeFromBookmark(podcast, `remove-bookmark-btn-` + podcast.item.id)"
                  >
                  </vs-button>


                </div>

              </div>
            </vx-card>
          </div>
        </div>
        <div class="row mt-8">
          <div class="col col-md-4"></div>
          <div class="col col-md-4">
            <vs-pagination v-if="totalPages > 1"
                           @change="goToPage"
                           v-model="currentPage"
                           :total="totalPages"></vs-pagination>
          </div>
          <div class="col col-md-4"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../../../../http/axios";
import mixins from "../../../../mixins";
import vSelect from "vue-select";

const PAGE_SIZE = 24;
export default {
  name: "Podcasts",
  components: {'v-select': vSelect},
  mixins: [mixins],
  data() {
    return {
      loading: true,
      access: true,
      podcasts: [],
      productId: this.$route.params.productId,
      product: null,
      subjects: [{id: '', label: "all subjects"}],
      englishLevels: [{id: '', label: "all levels"}],
      selectedSubject: {id: '', label: "all subjects"},
      selectedEnglishLevel: {id: '', label: "all levels"},
      totalPages: null,
      currentPage: 1,
      pageChanged: false
    }
  },
  mounted() {
    this.currentPage = this.$route.query.page ? +this.$route.query.page : 1;
    this.getProductDetail();
    this.getSubjects();
    this.getEnglishLevels();
    this.getPodcasts();
  },
  methods: {

    selectedFilter(){
      this.currentPage = 1;
      this.pageChanged = false;
      if (this.selectedSubject !== null && this.selectedEnglishLevel !== null){
        this.getPodcasts()
      }
    },

    getPodcasts() {

      this.loading = true;

      this.podcasts = [];
      let url = `/api/store/products/${this.productId}/items/` +
        `?page=${this.currentPage}&podcast__level=${this.selectedEnglishLevel.id}&podcast__subject=${this.selectedSubject.id}`
      axios.get(url, {
        headers: {
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        if (res.status === 200) {
          if (res.data.no_access){
            this.simpleNotify(this.NOTIFY_DANGER_TITLE, res.data.no_access, 'danger');
            this.access = false;
          }else {
            this.access = true;
            this.totalPages = Math.ceil(res.data.count / PAGE_SIZE);
            this.podcasts = res.data.results;
          }
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
        this.loading = false;

      })
    },

    goToPage() {
      if (this.pageChanged){
        this.$router.push({query: {page: this.currentPage}})
        this.getPodcasts();
        this.pageChanged = false;
      }else {
        this.pageChanged = true;
      }
    },

    removeFromBookmark(podcast, btnId) {
      this.startLoading(btnId);
      axios.delete(`/api/bookmarks/${podcast.bookmark}`, {
        headers: {
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        this.stopLoading(btnId);
        if (res.status === 204) {
          podcast.bookmark = null;
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
      });
    },

    addToBookmark(podcast, btnId) {
      this.startLoading(btnId);
      axios.post(`/api/bookmarks/`, {
        item_fid: podcast.fid
      }, {
        headers: {
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        this.stopLoading(btnId);
        if (res.status === 201) {
          podcast.bookmark = res.data.fid;
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
      });
    },

    getProductDetail() {
      axios.get(`/api/store/products/${this.productId}`,{
        headers: {
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        if (res.status === 200) {
          this.product = res.data
        }
      })
    },

    getSubjects() {
      axios.get(`/api/english/subjects`, {
        headers: {
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        if (res.status === 200) {
          res.data.forEach(s => {
            this.subjects.push({
              id: s.id,
              label: s.title
            })
          })
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
      })
    },

    getEnglishLevels() {
      axios.get(`/api/english/english-levels`, {
        headers: {
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        if (res.status === 200) {
          res.data.forEach(s => {
            this.englishLevels.push({
              id: s.id,
              label: s.title
            })
          })
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
      })
    },

    goToPodcastItem(podcastFid) {
      this.$router.push({
        name: "podcast-item",
        params: {
          itemFid: podcastFid,
          productId: this.productId
        }
      })
    },
  }
}
</script>

<style lang="scss" scoped>
.sticky-card{
  padding: .5rem;
}

.podcast-thumbnail-text{
  font-weight: 900;
  font-size: x-large;
  font-family: BalooBhai!important;
}

@media only screen and (min-width: 770px) {
  .scoped-sticky-pos {
    position: -webkit-sticky;
    position: sticky;
    top: 70px;
  }
}

@media only screen and (max-width: 480px) {
  .sm-horizontal-center {
    margin: 0.5rem auto;
  }
}
</style>

